*, ::after, ::before {
  box-sizing: border-box;
}

body {
  padding-top: 5rem;
}

.App {
  text-align: center;

}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.section {
  margin-bottom: 20px;
  border-bottom: 1px solid #000000;
  padding: 10px 0 10px 0
}
.section h4 {
  margin-top: 10px;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.owl-carousel .owl-nav .owl-prev {
  left: -25px;
}
.owl-carousel .owl-nav .owl-prev .fa {
  left: 12px;
  position: absolute;
  top: 12px;
}
.owl-carousel .owl-nav .owl-next {
  right: -25px;
}

.owl-carousel .owl-nav .owl-next .fa {
  right: 12px;
  position: absolute;
  top: 12px;
}

.owl-carousel .owl-nav .owl-prev, .owl-carousel .owl-nav .owl-next {
  font-size: 28px;
  color: #000000;
  background: #fff;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  box-shadow: 0 2px 6px rgba(0,0,0,0.6);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 99;
  outline: none;
  transition: all 0.3s ease;
  margin-top: -36px;
}

.itemwrap {
  position: relative;
  overflow: hidden;
  
}
.imagewrap {
  height: 350px;
  overflow: hidden;
  border-radius: 6px;
  position: relative;
  z-index: 1;
}
.picWrap {
  width: 100%;
  height: 100%;
}
.actionWrap {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  padding: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0,0,0,0.3);
  opacity: 0;
  transition: opacity 0.3s ease;
}
.actionWrap a {
  display: block;
  color: #fff !important;
  font-size: 16px;
  font-weight: 600;
  text-transform: uppercase;
  padding: 6px 24px;
  border: 2px solid #fff;
  border-radius: 6px;
  cursor: pointer;
}

.itemwrap:hover .actionWrap {
  opacity: 1;
}

.websitelist {
  text-align: left;
  background: #edecec;
  padding: 10px;
  margin: 20px 0 10px 0;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@media (min-width: 1200px) {
  .container {
      max-width: 90%;
  }
}